import {BASE_LOG_URL_API} from '../../url-conf'

export default {
  async logUser (username, password) {
    let headers = new Headers()
    headers.append('Content-Type', 'application/json')
    let init = {
      method: 'POST',
      headers: headers,
      mode: 'CORS',
      body: JSON.stringify({username: username, password: password}),
      'Access-Control-Allow-Origin': '*',
      cache: 'default'
    }
    try {
      let request = new Request(BASE_LOG_URL_API, init)
      let response = await fetch(request)
      response = await response.json()
      return response
    } catch (err) {
      return err
    }
  },

  async checkValidity (res) {
    if (res.status === 400) {
      return false
    }
    return res
  }
}



