import {LOG_USER} from './actions'
import {SET_USER_TOKEN} from './mutations'

let store = {
  actions: {
    LOG_USER
  },
  mutations: {
    SET_USER_TOKEN
  },
  state: {
    token: undefined,
    currentUser: '',
    concernedApplication: ''
  }
}

export default store



