import lazyLoading from './lazyLoading'

export default {
  name: 'Login',
  path: '/auth/login',
  component: lazyLoading('auth/login/Login'),
  meta: {
    default: false,
    title: 'Dconnexion',
    iconClass: 'vuestic-icon',
    expanded: false
  }
}



