import loginService from './services'
import {Dispatcher} from '../../Bus'

export const LOG_USER = async (username, password) => {
  await loginService.logUser(username, password).then(res => {
    if (res !== 400) {
      return Dispatcher.$emit('SET_USER_TOKEN', res)
    }
  })
}



